import React, { useState, useEffect } from "react";
import classNames from "classnames";
import elv from "../images/elv.png";
import arrow from "../icons/arrow.png";
import css from "./rsvp.less";

import * as firebase from "firebase/app";
require("firebase/firestore");

const RSVP = () => {
  const [code, setCode] = useState("");
  const [loading, setLoading] = useState(false);
  const [editing, setEditing] = useState(false);
  const [error, setError] = useState(null);
  const [data, setData] = useState(null);
  const [currentUser, setCurrentUser] = useState(null);

  const onChange = async e => {
    setError(null);
    const newCode = e.target.value.toUpperCase();

    if (newCode.length <= 5) {
      setCode(newCode);
    }

    if (newCode.length === 5) {
      const email = `${newCode}@marieogdaniel.no`;
      const password = `000-${newCode}`;

      setLoading(true);

      try {
        await firebase.auth().signInWithEmailAndPassword(email, password);
        setEditing(true);
        setLoading(false);
      } catch (err) {
        setError("Ugyldig invitasjonskode");
        setCode("");
        setLoading(false);
      }
    }
  };

  useEffect(() => {
    firebase.auth().onAuthStateChanged(setCurrentUser);
  }, []);

  useEffect(() => {
    if (currentUser) {
      const invitationCode = firebase
        .auth()
        .currentUser.email.slice(0, 5)
        .toUpperCase();

      const doc = firebase
        .firestore()
        .collection("data")
        .doc(invitationCode);

      doc
        .get()
        .then(doc => {
          setData(doc.data());
        })
        .catch(err => {
          setError("En feil har oppstått!");
          console.log("No data..", err);
        });
    }
  }, [currentUser]);

  const expandedClass = { [css.expanded]: currentUser !== null && !loading };

  return (
    <div className={css.outerContainer}>
      <div className={classNames(css.container, expandedClass)}>
        {loading ? (
          <div> Laster </div>
        ) : currentUser ? (
          <div>
            Logga inn!
            <button onClick={() => firebase.auth().signOut()}>Logg ut</button>
          </div>
        ) : (
          <>
            <div className={css.upsell}>Gi beskjed her</div>
            <img src={arrow} alt="" className={css.arrow} />
            <div className={css.header}>RSVP</div>
            <div className={css.description}>
              <div>
                Håper at du har anledning til å være med på feiringen av dagen
                vår
              </div>
              <div>
                Fortell oss om du kan komme eller ikke ved å taste inn din unike
                kode fra innbydelsen
              </div>
            </div>
            <input
              value={code}
              onChange={onChange}
              className={css.inputField}
              placeholder="Skriv her.."
              disabled={loading}
            />
            {error && <div className={css.error}>{error}</div>}
          </>
        )}
      </div>
      <img
        src={elv}
        alt="Daniel og Marie ved kanal"
        className={classNames(css.elvImage, expandedClass)}
      />
    </div>
  );
};

export default RSVP;
